
const modal = document.querySelector('.modal');
const sideMenu = document.querySelector('.side-menu');

//    data-
document.querySelectorAll('[data-action]').forEach(element => {
  element.addEventListener('click', (e) => {
    e.preventDefault();
    const action = e.currentTarget.getAttribute('data-action');
    const type = e.currentTarget.getAttribute('data-type');

    switch (action) {
      case 'open-modal':
        modalIsActive(type);
        break;
      case 'close-modal':
        modalIsActive(); 
        break;
      case 'open-side-menu':
        sideMenuIsActive(true);
        break;
      case 'close-side-menu':
        sideMenuIsActive(false);
        break;
    }
  });
});

//       overlay
const modalLayout = modal.querySelector('.modal__layout');
modalLayout.addEventListener('click', (e) => {
  if (e.target === modalLayout) {
    modalIsActive(); //   
  }
});



//    
function modalIsActive(value) {

// /    
  if (value === "message" || value === "call") {
    document.body.classList.add("no-scroll");
  } else {
    document.body.classList.remove("no-scroll");
  }
  
//     
  if (value === "message") {
    modal.classList.add("open");
    modal.classList.remove("close");

    const title = document.querySelector('.title__text--modal');
    const content = document.querySelector('.modal__content');

    title.textContent = ' ';
    content.innerHTML = `
      <div class="modal__feedback">
        <input class="input input--custom" type="text" placeholder="">
        <input class="input input--custom" type="text" placeholder="">
        <input class="input input--custom" type="text" placeholder=" ">
        <textarea class="input input--custom" placeholder="" cols="10" rows="10" maxlength="500" required></textarea>
      </div>
    `;
  } else if (value === "call") {
    modal.classList.add("open");
    modal.classList.remove("close");

    const title = document.querySelector('.title__text--modal');
    const content = document.querySelector('.modal__content');

    title.textContent = ' ';
    content.innerHTML = `
      <div class="modal__call">
        <input class="input input--custom" type="text" placeholder="">
      </div>
    `;
  } else {
    modal.classList.add("close");
    modal.classList.remove("open");
  }
}

//   -
function sideMenuIsActive(value) {
  if (value) {
    sideMenu.classList.add("open");
    sideMenu.classList.remove("close");
  } else {
    sideMenu.classList.add("close");
    sideMenu.classList.remove("open");
  }
}

